import axios from "axios";

export default{
	/**
     * getaxios.get(url, [options])
     * @param url { String } - url
     * @param options { Object } - options{params:{key:11}}
     * @param cb { Function } - 
     */
	getData: function(url, cb, options){
		axios.get(url, options).then(cb).catch(response => {
			if(response instanceof Error){
				//
			    console.log('Error:',response.message);
			}else {
				 //2xx
				console.log(response.data);
				console.log(response.status);
				console.log(response.header);
				console.log(response.config);
			}
		});
	},
	/**
     * postaxios.post(url, [options])
     * @param url { String } - url
     * @param data { Object } - bodynullbody{key:11}
     * @param options { Object } - optionsnull{params:{key:11}}
     * @param cb { Function } - 
     */
	 postData: function(url, data, cb, options){
		axios.post(url, data, options).then(cb).catch(response => {
			if(response instanceof Error){
				//
			    console.log('Error:',response.message);
			}else {
				 //2xx
				console.log(response.data);
				console.log(response.status);
				console.log(response.header);
				console.log(response.config);
			}
		})
	},
	/**
	 * 
	 */
	upload:function(vm, e, fname, url, cb){
		var formData = new FormData();
		var file = e.target.files[0];
		//
		formData.append(fname, file);
		console.log(formData);
		//split
		var src = file.name;
		var formart = src.split(".")[1];
		var config = {
        	headers:{'Content-Type':'multipart/form-data'}
      	};
		//
		if(formart == "jpg" || formart == "png" || formart == "jpeg" ||
				formart == "JPG" || formart == "PNG" || formart == "JPEG"){
			axios.post(url, formData, config).then(cb).catch(response => {
				if(response instanceof Error){
					console.log('Error',response)
				}else{

				}
			});
		}else{
			// return  vm.$dialog.toast({
			//             mes: '',
			//             timeout: 1000,
			//             icon: 'error'
			//         });
		}

	},
	uploadMore:function(vm, e, fname, url, cb){
		var formData = new FormData();
		$.each(e, function(index,item) {
			//
			formData.append(fname, item);
		});
		console.log(e,formData);
		var config = {
        	headers:{'Content-Type':'multipart/form-data'}
      	};
		axios.post(url, formData, config).then(cb).catch(response => {
			if(response instanceof Error){
				console.log('Error',response)
			}else{

			}
		});

	},

	//   String
    // (y) 1-4 (S) 1 ( 1-3 )
    // (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
    // (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
    formatDate: function(str, fmt) { //author: meizz
        fmt = fmt || 'yyyy-MM-dd';
				if(typeof str === 'string') str.replace(/-/g, '/')
				var date = new Date(str);
        var o = {
            "M+": date.getMonth() + 1, //
            "d+": date.getDate(), //
            "h+": date.getHours(), //
            "m+": date.getMinutes(), //
            "s+": date.getSeconds(), //
            "q+": Math.floor((date.getMonth() + 3) / 3), //
            "S": date.getMilliseconds() //
        };
        if (/(y+)/.test(fmt))
            fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt))
                fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return fmt;
    },

    /**
     *  "2016-08-15T16:00:00.000Z"   yyyy-MM-dd
     */
    changeDate: function(time, fmt){
    	var d = new Date(time);
			var date = `${d.getFullYear()}-${(d.getMonth()+1)}-${d.getDate()} ${d.getHours()}:${d.getMinutes()}:${d.getSeconds()}`;
			date = this.formatDate(date,fmt);
			return date;
    },

    /**
     * 
     * @param strBirthday {String} -  yyyy-MM-dd
     */
    calculateAge: function(strBirthday){
    	strBirthday = this.changeDate(strBirthday)
		var returnAge;
	    var strBirthdayArr=strBirthday.split("-");
	    var birthYear = strBirthdayArr[0];
	    var birthMonth = strBirthdayArr[1];
	    var birthDay = strBirthdayArr[2];

	   	var  d = new Date();
	    var nowYear = d.getFullYear();
	    var nowMonth = d.getMonth() + 1;
	    var nowDay = d.getDate();

	    if(nowYear == birthYear){  //
	    	var monthDiff = nowMonth - birthMonth; //
	    	if(monthDiff == 0){
	    		var dayDiff = nowDay - birthDay;//
	    		returnAge = dayDiff >= 0 ? dayDiff + '' : -3;
	    	}else if(monthDiff > 0){
	    		returnAge = monthDiff + '';
	    	}else{
	    		returnAge = -2; //-1  
	    	}

	    }else{
	        var ageDiff = nowYear - birthYear ; //
	        if(ageDiff == 1){
	            if(nowMonth == birthMonth) {
	                var dayDiff = nowDay - birthDay;//
	                returnAge = dayDiff < 0 ? '11' : ageDiff + '';
	            }else{
	                var monthDiff = nowMonth - birthMonth;//
	                returnAge = monthDiff < 0 ? 12 + monthDiff + ''  : ageDiff + '';
	            }
	        }else if(ageDiff > 1){
	        	if(nowMonth == birthMonth) {
	                var dayDiff = nowDay - birthDay;//
	                returnAge = dayDiff < 0 ? ageDiff + '' : ageDiff-1 + '';
	            }else{
	                var monthDiff = nowMonth - birthMonth;//
	                returnAge = monthDiff < 0 ? ageDiff + '' : ageDiff-1 + '';
	            }
	        }else{
	            returnAge = -1;//-1  
	        }
	    }
	    return returnAge;//
    },
    /**
     * 
     */
    getNowDate: function(){
    	var  d = new Date();
	    var nowYear = d.getFullYear();
	    var nowMonth = d.getMonth() + 1;
	    var nowDay = d.getDate();

	    var date = nowYear + '-' + nowMonth + '-' + nowDay;
	    date = this.formatDate(date);
	    return date;
    },
    /**
     * 
     */
    getNowTime: function(){
    	var  d = new Date();
	    var nowYear = d.getFullYear();
	    var nowMonth = d.getMonth() + 1;
	    var nowDay = d.getDate();
	    var nowHour = d.getHours();
	    var nowMinutes = d.getMinutes();
	    var nowSeconds = d.getSeconds();

	    var date = nowYear + '-' + nowMonth + '-' + nowDay + ' ' + nowHour + ':' + nowMinutes + ':' + nowSeconds;
	    date = this.formatDate(date, "yyyy-MM-dd hh:mm:ss");
	    return date;
    },
    /**
     * 
     */
    completeTime: function(start, end){
	   var s = new Date(start);
	   var e = new Date(end);
	   if(s >= e){
	     return false;
	   }else{
	     return true;
	   }
	 },
	 /**
	  * + 
	  * @param value {String} - 
	  * @param format {String} -  yyyy-MM-dd hh:mm:ss
	  */
	returnDatetime: function(value, formart){
		var d = value.getFullYear() + '-' + (value.getMonth()+1) + '-' + value.getDate() + ' ' + value.getHours() + ':' + value.getMinutes()
		d = this.formatDate(d, formart);
		return d;
	},
    /**
     * 
     */
    alert: function(that, tips, tipTitle){
    	var titleText = tipTitle ? tipTitle : "";
		that.$messagebox({
			title: titleText,
			message: tips
		});
    },

    confirmTurn: function(that, tips, routeUrl, hasToast, cbText, tipTitle){
    	var titleText = tipTitle ? tipTitle : "";
    	var actionText = cbText ? cbText : '' ;
    	var url = routeUrl;
		that.$messagebox({
			title: titleText,
			message: tips,
			showCancelButton: true,
		}).then(action =>{
			if(action == 'confirm'){
				if(hasToast){
					that.$dialog.toast({
	                    mes: actionText,
	                    timeout: 1000,
	                    icon: 'success'
	                });
					setTimeout(function(){
						that.$router.replace(url);
					},1500)
				}else{
					that.$router.replace(url);
				}
			}
		});
   },

    confirmCallback: function(that, tips, confirmCb, tipTitle){
    	var titleText = tipTitle ? tipTitle : "";
		that.$messagebox({
			title: titleText,
			message: tips,
			showCancelButton: true,
		}).then(action =>{
			if(action == 'confirm'){
				confirmCb();
			}
			if(action == 'cancel'){
				//callback();
			}
		});
	},

	toastCallback: function(that, mesText, url){
		that.$dialog.toast({
            mes: mesText,
            timeout: 1000,
            icon: 'success'
        });
		setTimeout(function(){
			that.$router.replace(url);
		},1500)
	}

}



