// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import store from './store';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import axios from 'axios';
import $ from 'jquery';
/**/
import './public/validate.js';
import VeeValidate from 'vee-validate';
Vue.use(VeeValidate);
/*mintUI*/
import MintUI from 'mint-ui';
import 'mint-ui/lib/style.css';
/*YdUI*/
import YDUI from 'vue-ydui';
import 'vue-ydui/dist/ydui.rem.css';
//import 'vue-ydui/dist/ydui.px.css';


/**/
import 'lib-flexible/flexible.js';

import utils from './public/utils';
import urls from './public/api';
import config from './public/config';
Vue.prototype.utils = utils;
Vue.prototype.urls = urls;
Vue.prototype.config = config;

Vue.prototype.$axios = axios;

Vue.use(MintUI);
Vue.use(YDUI);
Vue.use(VueRouter);
Vue.use(VueResource);

Vue.config.productionTip = false;

// vuex
// if (window.sessionStorage.userInfo) {
//     store.dispatch('setUserInfo', JSON.parse(window.sessionStorage.userInfo));
// }

/*Vuextrue*/
const List = ['/forget', '/login', '/register'];

router.beforeEach((to, from, next) => {
  //
  // console.log(from,'---->',to )
  if (store.getters.token) {
      if(List.indexOf(to.path) !== -1) {
        next({path: '/'})
      } else {
        //F5vex
        if(!store.getters.userInfo){ // tokenuserInfo
            // console.log('',store.getters.userInfo)
           store.dispatch('GetUserInfo').then((res) => { // user_info
              // console.log('',res,store.getters.userInfo);
              next();
           }).catch(err => {
               console.log(err);
           });
        }else{
            next();
        }
      }
  }else{
    	if(List.indexOf(to.path) !== -1){  //
        next();
    	}else{
    		next({path: '/login'});
    	}
  }
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



